/* Общие стили */
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background: linear-gradient(-45deg, #18181b 0%, #1e1b4b 25%, #312e81 50%, #181ba7 75%, #721cc2 100%);
  background-size: 800% 800%;
  animation: gradientFlow 20s ease-in-out infinite;
  color: #e0e0e0;
}

@keyframes gradientFlow {
  0% {
    background-position: 0% 50%;
  }
  20% {
    background-position: 50% 100%;
  }
  40% {
    background-position: 100% 50%;
  }
  60% {
    background-position: 50% 0%;
  }
  80% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.cursor {
  display: inline-block;
  width: 2px;
  background-color: #8b5cf6;
  margin-left: 2px;
  animation: blink 1s infinite;
}

@keyframes blink {
  0%, 50%, 100% { opacity: 1; }
  25%, 75% { opacity: 0; }
}

a {
  text-decoration: none;
  color: inherit;
}

/* Header */
header {
  position: fixed;
  top: 0;
  width: 100%;
  background: rgba(15, 23, 42, 0.95);
  box-shadow: 0 2px 8px rgba(0,0,0,0.5);
  z-index: 50;
}

header nav {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
}

header nav ul {
  list-style: none;
  display: flex;
  gap: 1.5rem;
}

header nav ul li a:hover {
  color: #a855f7;
}


/* Hero */
.hero {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  overflow: hidden;
}
.hero-buttons {
  margin-top: 1.5rem;
}
.hero-title {
  font-size: 4rem;
  font-weight: 800;
  background: linear-gradient(90deg, #6366f1, #a855f7, #3b82f6, #8b5cf6);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: heroGradientText 5s linear infinite;

}
.hero-description {
  margin-bottom: 30px;
}
@keyframes heroGradientText {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.video-editing{
  color: #94a3b8;
}
.video-editing:hover{
  color: #a855f7;
}
.hero p {
  margin-top: 1rem;
  font-size: 1.25rem;
  max-width: 600px;
  color: #cbd5e1;
}

.hero .btn-glow {
  position: relative;
  padding: 0.75rem 2rem;
  margin-top: 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  color: #fff;
  background: #6366f1;
  overflow: hidden;
  transition: transform 0.3s, box-shadow 0.3s;
   
  }


.hero .btn-glow:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px #a855f7, 0 0 25px #3b82f6;
}

/* About */
section {
  padding: 5rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

section h2 {
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
  color: #a855f7;
}

section p {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  line-height: 1.6;
  color: #cbd5e1;
}

/* Skills */
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}

.skills span {
  padding: 0.5rem 1.25rem;
  background: linear-gradient(90deg, #6366f1, #8b5cf6);
  border-radius: 12px;
  color: #fff;
  font-weight: 600;
  transition: transform 0.3s, box-shadow 0.3s;
  cursor: pointer;
}

.skills span:hover {
  transform: scale(1.1);
  box-shadow: 0 0 15px #a855f7, 0 0 25px #3b82f6;
}

/* Projects */
.projects-grid {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  justify-items: center;
}

.project-card {
  background: linear-gradient(135deg, #312e81 60%, #6366f1 100%);
  color: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 18px rgba(60, 30, 130, 0.18);
  padding: 20px;
  width: 100%;
  max-width: 300px;
  transition: transform 0.3s, box-shadow 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project-card:hover {
  transform: translateY(-5px) scale(1.04);
  box-shadow: 0 8px 32px #a855f7, 0 0 24px #6366f1;
}

.project-card h3 {
  margin-bottom: 10px;
  color: #8b5cf6;
}

.project-card p {
  font-size: 14px;
  margin-bottom: 10px;
}

.tech-stack {
  margin-bottom: 15px;
}

.tech {
  display: inline-block;
  background: #312e81;
  padding: 3px 8px;
  border-radius: 6px;
  font-size: 12px;
  margin-right: 5px;
  margin-bottom: 5px;
}

.project-links a {
  margin-right: 10px;
  text-decoration: none;
  font-weight: bold;
  color: #e0e0e0;
  transition: color 0.3s;
}

.project-links a:hover {
  color: #8b5cf6;
}

/* Current Projects */

#current-projects {
  padding: 50px 20px;

  color: #e0e0e0;
  text-align: center;
}

#current-projects h2 {
  margin-bottom: 40px;

  color: #8b5cf6;
}

/* .projects-grid, .project-card, .tech-stack, .tech, .project-links остаются как в Projects */


/* Contact Form */
form {
  margin-left: auto;
  margin-right: auto;
  max-width: 600px;
  background: #1e1b4b;
  padding: 2rem;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

form input, form textarea {
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;

  border-radius: 12px;
  border: none;
  outline: none;
}

form button {
  width: 100%;
  padding: 0.75rem;
  border-radius: 12px;
  background: #6366f1;
  color: #fff;
  font-weight: 600;
  transition: transform 0.3s, box-shadow 0.3s;
}

form button:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px #a855f7, 0 0 25px #3b82f6;
}


/* Footer */
footer {
  padding: 2rem;
  text-align: center;
  background: #1e1b4b;
  color: #94a3b8;
}

a{
  text-decoration: none;
}
.telegram-link{
  color: #3131f7;
}

/* videoPage */
.video-page {
  background: linear-gradient(135deg, #0f172a, #1e1b4b);
  min-height: 100vh;
  color: #e0e0e0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.video-container {
  text-align: center;
  padding: 80px 20px;
  
}

.video-container h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #8b5cf6;
}

.video-container h3 {
  font-size: 20px;
  margin-bottom: 15px;
}

.video-container p {
  font-size: 16px;
  margin-bottom: 40px;
}
.clickable {
  cursor: pointer;
  color: #94a3b8;
  transition: color 0.3s;
}

.clickable:hover {
  color: #a855f7;
  transition: color 0.5s;
}

/* Price Card Styles */
.price-section {
  max-width: 900px;
  margin: 60px auto 40px auto;
  padding: 2rem;
  border-radius: 24px;
}

.price-section h2 {
  text-align: center;
  color: #a855f7;
  margin-bottom: 2.5rem;
  font-size: 2.5rem;
  font-weight: 800;
  letter-spacing: 1px;
}

.price-list {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  padding: 0;
}

.price-item {
  background: linear-gradient(135deg, #312e81 60%, #6366f1 100%);
  color: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 18px rgba(60, 30, 130, 0.18);
  padding: 2rem 1.5rem;
  min-width: 220px;
  max-width: 280px;
  flex: 1 1 220px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.25s, box-shadow 0.25s;
}

.price-item:hover {
  transform: translateY(-8px) scale(1.04);
  box-shadow: 0 8px 32px #a855f7, 0 0 24px #6366f1;
}

.price-item h3 {
  margin-bottom: 1rem;
  font-size: 1.2rem;
  color: #a855f7;
  text-align: center;
  font-weight: 700;
}

.price-item p {
  margin: 0.5rem 0;
  text-align: center;
  font-size: 1rem;
}

.price-item strong {
  font-size: 1.4rem;
  color: #fff;
  background: linear-gradient(90deg, #a855f7, #6366f1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 800;
}

/* Адаптив */
@media (max-width: 700px) {
  .price-list {
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
  }
  .price-item {
    max-width: 95vw;
    min-width: 0;
    padding: 1.5rem 1rem;
  }
}

@media (max-width: 600px) {
  .hero .btn-glow,
  form button {
    padding: 0.6rem 1.2rem;
    font-size: 1rem;
    border-radius: 10px;
  }
}

@media (max-width: 400px) {
  .hero .btn-glow,
  form button {
    padding: 0.5rem 0.8rem;
    font-size: 0.9rem;
    border-radius: 8px;
  }
}